import {
  CHANGE_INPUT,
  REQUEST_MUSIC_SUCCESS,
} from "./constants";
import { musicState } from "./components/Card/CardState";

const initialState = {
  input: "Justin Bieber"
};


export const searchMusic = (state = initialState, action = {}) => {
    switch(action.type) {
        case CHANGE_INPUT: return Object.assign({}, state, {input: action.payload});
        default: return state;
    }
};



export const requestMusic = (state = musicState, action={}) => {

  switch(action.type) {
    case REQUEST_MUSIC_SUCCESS: return Object.assign({}, state, {music: action.payload});
    default: return state;
}

}
